.alert {
  opacity: 0;
  transform: translateX(100%);
  animation: slideIn 0.3s ease-out forwards;
}

.alert.exit {
  animation: slideOut 0.3s ease-in forwards;
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideOut {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    transform: translateX(100%);
  }
}
